.slick-slide {
    width:  100%;
 }    // margin-right:  47px


.sub-line {
    width: 68px;
    height: 1px;
    background-color: $cormax;
    margin-right: 38px; }

.sub-title {
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1px;
    color: $cormax;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }

.sub-info {
    display: flex;
    align-items: center; }

.arrows {
    ul {
        display: flex; } }

.arrows {
    li:first-child {
        margin-right: 40px; } }

.prev {
    height: 20px;
    width: 40px;
    display: block;
    cursor: pointer;
    background: url('img/icons/arl.svg') no-repeat; }

.next {
    height: 20px;
    width: 40px;
    display: block;
    cursor: pointer;
    background: url('img/icons/arrowr.svg') no-repeat; }


.likes:hover > * {
    fill: #F25C05; }


.myBtn {
    font-weight: 600;
    text-align: center;
    letter-spacing: 1.5px;
    color: #fff;
    position: relative;
    padding: 15px 64px;
    background-color: #F25C05;
    &:hover:disabled {
        &:before,&:after {
            content: '';
            opacity: 0;
            transition: all 0.5s ease !important; } }
    &:disabled {
        background: #D8D8D8; }
    &:before,&:after {
      content: '';
      opacity: 0;
      transition: all 0.5s ease !important; }
    &:hover:before {
        transition: all 0.5s ease;
        content: '';
        width: 100%;
        height: 100%;
        border: 1px solid black;
        position: absolute;
        top: -6px;
        left: -6px;
        z-index: 1;
        opacity: 1; }
    &:hover:after {
        transition: all 0.5s ease;
        content: '';
        width: calc(100% - 6px);
        height: calc(100% - 6px);
        border-bottom: 1px solid white;
        border-right: 1px solid white;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 3;
        opacity: 1; } }


.a-origin {
    color: $orig !important;
    font-weight: bold; }

.input-wrapper {
    position: relative;
    line-height:  14px;
    margin:  0 10px;
    display: inline-block;
    margin: 0px !important;
    input {
        margin: 0px !important;
        background-color:  #ffffff;
        padding: 10px 15px;
        border: 1px solid $cormax;
        &:focus + label {
            transform: translateY(-22.5px) !important;
            transition: transform 100ms ease; }
        &:invalid + label {
            transform: translateY(0); } }

    label {
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.5px;
        color: #A69794;
        background-color: #ffffff;
        padding: 0 4px;
        position: absolute;
        left: 10px;
        top: 13px;
        transition: transform 100ms ease;
        transform: translateY(-22.5px); } }


