body {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: $cormid;
    background-color: #ffffff; }
.out {
    max-width: 1200px;
    margin: 0 auto; }

a {
    text-decoration: none !important; }

.gm-style {
    display: none !important; }
/// HEADER

header {
    background-color: #ffffff; }

.main-header {
    display: flex;
    width: 100%;
    min-height: 80px !important;

    &_blockL {
        display: flex;
        align-items: center;
        width: 70%;
        .header-logo {
            width: 17%; }
        menu {
            @include r(1000) {
                display: none; }
            width: 83%;
            display: flex;
            justify-content: space-between;
            li:last-child {
                margin-right: 100px; }
            li {
                list-style: none;
                display: inline;
                a {
                    color: $cormax;
                    font-weight: 500;
                    font-size: 14px;
                    letter-spacing: 0.005em; }
                a:hover,a:active {
                    color: $orig; } } } }
    &_blockR {
        @include r(1000) {
            width: 60%; }
        @include r(800) {
            width: 100%; }
        width: 30%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .icons {
            ul {
                li {
                    list-style: none;
                    display: inline;
                    padding-right: 25px; } } }
        .lang {
            font-weight: 600;
            letter-spacing: 0.005em;
            a {
                color: $cormax; } }
        .navbar-1 {
            display: none;
            @include r(1000) {
                display: block; } } } }

.nav-link {
    color: $cormax;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.005em; }

.nav-link:hover,.nav-link:active {
    color: $orig; }






.product-slider_nav {
    &__l {
        display: flex;
        justify-content: space-between; }
    &__r {
        margin-top: 40px; }
    &__btn {
        display: flex;
        justify-content: center;
        margin-top: 65px !important; } }



.product-card {
    width: 260px !important;
    position: relative;
    transition: all 2s linear !important;

    .hover_el {
        width: 260px;
        height: 356px;
        border: 1px solid #705C5C;
        position: absolute;
        top: 10px;
        left: 12px;
        display: none;
        transition: all 2s linear !important;
        z-index: 9999999 !important; }
    img {
        width: 260px;
        height: auto; }

    &_title {
        line-height: 17px;
        color: #493C3A;
        margin-top: 20px; }
    &_price {
        font-weight: 600;
        font-size: 16px;
        color: #362320;
        margin-top: 12px; }
    &:hover >.hover_el {
        display: block; } }


.slick-slide {
    .slick-slider-img {
        margin: 10px;
        padding: 2%;
        position: relative;
        border: 5px solid #FFF;
        display: block;
        width: 100%; } }


.likes {
    float: right;
    transition: all 0.5s ease !important; }







.eco-sckin {
    margin-top: 135px;
    margin-bottom: 135px;
    &__title {
        font-style: normal;
        font-weight: 600;
        font-size: 115px;
        line-height: 132px;
        text-transform: uppercase;
        color: #362320;
        @include r(800) {
            font-size: 35px; } }
    &__info-block {
        display: flex;
        justify-content: flex-end;
        .eco-line {
            width: 100%;
            height: 1px;
            background:#362320 {}
            position: absolute;
            top: -60px; }
        .block {
            display: flex;
            width: 58%;
            position: relative  !important;
            justify-content: space-between;
            div:last-child {
                margin-right: 0px !important; } } }
    &__btn {
        display: flex;
        justify-content: center;
        margin-top: 65px !important;
        button {
            padding: 15px 32px !important; } } }

.ib-reason {
    margin-right: 28px;
    width: 100%;
    &__title {
        display: flex;
        align-items: center;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1px;
        margin-bottom: 50px;
        img {
            margin-right: 27px; } }
    p {
        font-size: 15px;
        line-height: 26px;
        letter-spacing: 0.5px;
        color: #493C3A; } }




.brand {
    min-height: 660px;
    display: flex;
    position: relative;
    &__l {
        width: calc( 50%  - 99px );
        background-color:#FAFAFA {}
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-flow: column wrap;
        padding-left: 130px;
        &_btn {
            button {
                padding: 15px 23px !important; } }
        &_title {
            font-style: normal;
            font-weight: 600;
            font-size: 30px;
            line-height: 38px;
            letter-spacing: 1px;
            color: #362320;
            margin-bottom: 57px; } }

    &__r {
        width:  calc( 50%  + 99px )  !important;
        background-size: 100%;
        background-repeat: no-repeat;
        height: 668px; } }

.photo-middle {
    position: absolute;
    width: 217px;
    height: 290px;
    left: calc( 50%  - 140px );
    top: 50%;
    transform: translate(-50%,-50%); }

//MAP

.map-nav {
    display: flex;
    margin-top: 150px;
    width: 100%;
    align-items: flex-start;
    .sub-info {
        margin-top: 15px;
        padding-left: 99px;
        width: calc( 50%  - 99px ); }
    .tabs {
        display: flex;
        padding-bottom: 65px;
        li {
            font-weight: 600;
            position: relative;
            font-size: 30px;
            line-height: 37px;
            text-align: center;
            letter-spacing: 1.3px;
            color: #362320;
            text-shadow: 0px 5px 37px rgba(255, 255, 255, 0.50201);
            cursor: pointer; }
        li:first-child {
            margin-right: 65px; } } }

.activ {
    &::after {
       content: '';
       width: 100%;
       height: 1px;
       background: $orig;
       position: absolute;
       top: 120%; } }

.city {
    margin-bottom: 15px;
    li {
        list-style: none;
        display: inline;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 1px;
        color: #362320;
        padding-left: 42px;
        cursor: pointer; }
    li:first-child {
        padding-left: 0px !important; } }


.map-content {
    display: flex;
    min-height: 480px;
    &__l {
        width:  calc( 50%  - 99px )  !important; }


    &__r {
        width:  calc( 50%  + 99px )  !important; } }

.tab-con {
    padding-left: 99px;
    display: flex;
    flex-wrap: wrap; }


.in-map-block {
    width: 50% !important;
    .tm-title {
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1px;
        color: #382522;
        margin-bottom: 12px; }
    .tm-text {
        margin-bottom: 45px;
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 0.5px;
        color: #493C3A; } }

.tab-content {
    display: none;
    min-height: 480px;
    width: 100%; }


.current {
    display: inherit; }

.leaflet-container {
    background-color:rgba(255,0,0,0.0) {} }

#map {
    width: 100% !important;
    height: 480px !important;
    background: silver !important; }

.hide {
    display: none !important; }


.contact {
    &__btn {
        display: flex;
        justify-content: center;

        button {
            padding: 15px 50px !important; } }
    &__info-block {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        p {
            font-size: 14px;
            width: 100%;
            line-height: 26px;
            letter-spacing: 0.5px;
            color: #493C3A;
            text-shadow: 0px 5px 37px rgba(255, 255, 255, 0.50201); }
        form {
            width: 100%;
            margin: 40px 0px 27px 0;
            .input-wrapper {
                float: left;
                width: 47%;
                input {
                    width: 100% !important; } }
            .input-wrapper:first-child {
                margin-right: 6% !important; }
            .textarea-wrapper {
                width: 100%;
                margin-top: 40px !important;
                textarea {
                    width: 100%; } } }
        .contact-line {
            width: 100%;
            height: 1px;
            background:#362320 {}
            position: absolute;
            top: -60px; }
        .block {
            width: 58%;
            position: relative; } } }

#email,#user {
    float: left; }


footer {
    background-color: $white;
    padding: 70px 0 20px 0; }

.footer {
    display: flex;
    justify-content: space-between;
    &__btn {
        button {
            padding: 15px 20px !important; } }
    &__email {
        display: flex;
        .em-btn {
            .input-wrapper {
                width: 100%;
                margin-bottom: 26px !important;
                input {
                    width: 100%;

                    background-color: $white; }
                label {
                    background-color: $white; } } } }
    &__list-info {
        &__menu {
            float: left;
            li {
                list-style: none;
                margin-top: 20px; }
            li:first-child {
                margin-top: 0px !important; }
            a {
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: 0.5px;
                color: #362320; }
            a:hover,.act {
                font-size: 14px;
                line-height: 17px;
                letter-spacing: 0.5px;
                color: #F25C05; } }
        &__pages {
            float: left;
            margin-left: 70px;
            li {
                list-style: none;
                margin-top: 22px; }
            li:first-child {
                margin-top: 0px !important; }
            a {
                font-weight: normal;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: 0.5px;
                color: #362320; }
            a:hover,.act {
                font-size: 14px;
                line-height: 17px;
                letter-spacing: 0.5px;
                color: #F25C05; } } } }


.act {
    font-weight: 600 !important; }

.in_block {
    position: relative;
    margin-bottom: 37px;
    &_title {
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 1px;
        color: #362320;
        margin-bottom: 8px; }
    &_txt {
        font-size: 14px;
        line-height: 26px;
        letter-spacing: 0.5px;
        color: #493C3A;
        text-shadow: 0px 5px 37px rgba(255, 255, 255, 0.50201); }

    .f-line {
        width: 68px;
        height: 1px;
        background-color: #362320;
        position: absolute;
        left: -100px;
        top: 12px; } }

.fsocia {
    padding-left: 40px;
    display: flex;
    flex-flow: column wrap;
    a {
        margin-top: 32px; }
    a:first-child {
        margin-top: 0px !important; } }
